import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const AirFilter = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Воздушный фильтр
      </PanelHeader>

    <Group>
     <Div>
      <center>Воздушный фильтр</center>
        <p>Чтобы достичь оптимальной производительности, двигателям внутреннего сгорания необходимо поступление чистого воздуха. Загрязнения очень опасны для функционирования двигателя: если загрязнения в воздухе, такие как сажа или пыль, попадают в камеру сгорания, то они начинают абразивный износ стенок блока цилиндров, вызывая преждевременный износ двигателя.</p>
        <p>Воздушные фильтры становятся надежной преградой для всех частиц загрязнений. Независимо от того, что это – пыль, пыльца, песок, сажа или даже капли воды. Благодаря высокой степени отделения загрязнений и высокой механической стабильности воздушные фильтры отделяют даже мельчайшие частицы из поступающего воздуха. Это позволяет подобрать соотношение компонентов горючей смеси и гарантирует стабильную работу двигателя.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Виды воздушных фильтров</center>
        <p>Фильтрующие элементы выпускаются трех конструктивных типов:</p>
          <li>Цилиндрические</li>
          <li>Панельные</li>
          <li>Бескаркасные</li>
        <p>Отметим особенности, имеющие непосредственное отношение к эксплуатации.</p>
        <p>Картон — распространенный материал для воздушных фильтров. Но в некоторых странах сменные элементы делают из синтетических волокон. Эксплуатация их требует строгого соблюдения интервалов замены.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Когда следует менять воздушный фильтр?</center>
        <p>Водителю не обязательно знать когда нужно менять воздушный фильтр, это можно сделать визуально. Для этого достаньте фильтрующий элемент и осмотрите его. Если он чистый - ездить можно дальше без проблем. Если фильтрующий элемент грязный или в масле - это говорит о необходимости его замены. Каждый автолюбитель может отличить грязный фильтр от нового и чистого.</p>
        <p>Срок замены зависит от производителя, на большинстве машин он составляет 15 - 30 тысяч км. Так что, если собрались менять воздушный фильтр, загляните в техпаспорт машины и там будет точный срок. Или воспользуйтесь советом выше и осмотрите его визуально, если не знаете когда в последний раз он менялся.</p>
		<p>У дизельных двигателей с турбонаддувом к очистке воздуха предъявляются более жесткие требования. Это связано с особенностями их эксплуатации. Так что, у дизелей и у машин с турбиной, срок замены воздушного фильтра должен быть не реже чем 10-15 000 км.</p>
	 </Div>
    </Group>
	</Panel>
);

AirFilter.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default AirFilter;
