import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const DevicePG = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Устройство РКПП
      </PanelHeader>

    <Group>
     <Div>
      <center>Устройство РКПП</center>
        <p>Как бы там ни казалось, устройство роботизированной коробки передач достаточно простое. Это обыкновенная МКПП, которая имеет в своей конструкции дополнительные элементы. Они производят активацию и деактивацию сцепления и меняют передачи безошибочно. А вот принципы работы что у механической, что у «робота» одинаковые.</p>
        <p>Но, само собой, существуют отличия. Главным отличием можно считать наличие актуатора (исполнительное устройство). Именно он, при помощи сервоприводов, управляет работой сцепления. В свою очередь электронный блок контролирует актуатор.</p>
		<p>Привод сцепления и передач в роботизированных коробках может быть двух видов:</p>
		<li>Электрические. Именно в этом типе все функции выполнения команд берут на себя сервомеханизмы. В этом же виде иногда применяется гидромеханический блок, для того, чтобы перемещать механизм сцепления;</li>
		<li>Гидравлические. Здесь гидроцилиндры, управляемые при помощи клапанов электромагнита, осуществляют действия самого привода.</li>
        <p>Стоит заметить, что привод, работающий на электронике, имеет относительно невысокую скорость операций (передачи переключаются за 0,6 секунды), но это компенсируется меньшим потреблением энергии.</p>
		<p>В приводе на гидравлике необходимо поддерживать постоянное давление. По этой причине энергетические затраты довольно высокие. А вот скорость выполнения операций значительно выше. Поэтому их очень часто устанавливают на спортивные автомобили. Например, у Ламборджини Avendator скорость смены передач составляет 0,04 секунды.</p>
        <p>Это значит, что коробки с приводом, работающем на электричестве, ставят на бюджетные автомобили, а вот гидравлические более подходят для дорогих транспортных средств.</p>
		<p>Система электронного управления, включающая в себя датчики входа, электронный блок и устройство исполнения, осуществляет контроль за коробкой-роботом. Датчики входа прослеживают главные показатели КП: количество вращений и где расположен селектор. Если это гидравлический привод, то прослеживается температурный режим масла и давление. Все эти данные отправляются к блоку управления.</p>
        <p>На основе этой информации управленческий блок контролирует механизмы исполнения, исходя из встроенных алгоритмов. Электронный блок непосредственно работает и влияет на разные системы. Например:</p>
		<li>Систему управления двигателя. Именно она заставляет работать все системы двигателя автоматически. Сюда входят: впрыскивание горючего, управление воздушными массами, активация зажигания и т.д.;</li>
		<li>Антиблокировочную систему. Она нужна для того, чтобы предотвратить блокировку колес автомобиля во время торможения. Именно это обеспечивает минимальный тормозной путь на транспортных средства.</li>
		<p>В РКПП с приводом гидравлики встроен управленческий блок, главной задачей которого является контроль гидроцилиндров и давления. Конструктивно в нём больше элементов чем в электрическом:</p>
        <li>гидроаккумулятор — поддерживает необходимое давление в системе;</li>
		<li>групповая электропомпа — создаёт давление в системе;</li>
		<li>цилиндры включения передач и сцепления — управляют механизмами включения.</li>
		<p>Механизмами исполнения в коробке-роботе являются электродвигатель (в электронном приводе) и клапаны электромагнитов гидроцилиндра (при приводе на гидравлике).</p>
		<p>Большинство роботизированных коробок передач имеют гидравлический привод. Электрический привод часто используется в бюджетных автомобилях как наиболее дешёвый вариант.</p>
     </Div>
    </Group>
	</Panel>
);

DevicePG.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default DevicePG;
