import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const WhatMG = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Что такое МКПП
      </PanelHeader>

    <Group>
     <Div>
      <center>Что такое МКПП</center>
        <p>Механическая коробка передач — предназначенная для ступенчатого изменения передаточного отношения, в котором выбор передачи осуществляется вручную.</p>
        <p>Коробка передач обеспечивает использование оптимального режима работы двигателя при движении в различных условиях. В механической коробке передач это осуществляется за счёт осуществляемого водителем переключения передач, имеющих различное передаточное число.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Преимущества МКПП</center>
          <li>Высокое КПД.</li>
          <li>Имеет большой срок службы.</li>
          <li>Обширный выбор стилей езды в различных условиях эксплуатации автомобиля (например: бездорожье, грязь, гололед).</li>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Недостатки МКПП</center>
          <li>При длительной езде по городу или в пробках значительно больше утомляет водителя.</li>
          <li>Относительно небольшой ресурс механизма сцепления.</li>
     </Div>
    </Group>
	</Panel>
);

WhatMG.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default WhatMG;
