import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const OilFilter = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Масляный фильтр
      </PanelHeader>

    <Group>
     <Div>
      <center>Масляный фильтр</center>
          <p>Масляный фильтр относится к сменным элементам автомобильного двигателя, от которых зависит эффективность работы мотора, долговечность его эксплуатации. Регулярная замена масляного фильтра входит в эксплуатационные правила, поэтому грамотный автолюбитель должен разбираться в конструкции узла, уметь выбрать качественный фильтр.</p>
		  <p>Герметичность автомобильного двигателя, практически полностью заполненного маслом, не гарантирует постоянной чистоты смазки. Двумя главными причинами загрязнения масла становятся механические примеси, продукты неполного сгорания топлива. При этом загрязнение сажей более характерно для дизельных двигателей, а процент механических примесей непосредственно связан с пробегом автомобиля, износом мотора. Для защиты от примесей, постоянной очистки масла применяется масляный фильтр.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Конструкция и типы</center>
		  <p>В конструкциях двигателей применяется три основные системы очистки:</p> 
		  <li>Полнопоточный фильтр - в нем фильтрующий элемент включен в основную масляную линию. При каждом запуске двигателя все масло проходит через фильтр под действием масляного насоса.</li>
		  <li>Частичнопоточный фильтр - такой фильтр пропускает фильтруемое масло по частям, добиваясь чистоты при длительной работе.</li>
		  <li>Комбинированный фильтр - совмещает в себе частичнопоточную и полнопоточную фильтрацию масла. Это обуславливает более тщательную фильтрацию масла и, как результат, более долгий срок эксплуатации моторного масла и масляного фильтра.</li>
     </Div>
    </Group>
	</Panel>
);

OilFilter.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default OilFilter;
