import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const Turbine = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Турбина
      </PanelHeader>

    <Group>
     <Div>
      <center>В чем преимущества турбин на автомобиле?</center>
        <p>Турбированный силовой агрегат приобретает все большую популярность, и для этого есть множество причин, поскольку перечень преимуществ турбонагнетателя весьма обширен.</p>
		<p>Привлекательность турбины состоит в следующем:</p>
          <li>Увеличение мощности транспортного средства.</li>
		  <li>Снижение топливного расхода.</li>
		  <li>Экономичный.</li>
		  <li>Стабильное функционирование двигателя.</li>
		  <li>Экологичность.</li>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Как правильно выбрать турбину?</center>
        <p>Турбина и двигатель должны функционировать сбалансировано, и каждый тип мотора требует определенной турбины. Разумеется, лучше всего приобретать оригинальный турбонаддув, в этом случае производитель учитывает все особенности двигателей своих же автомобилей и выпускает турбины под конкретные силовые агрегаты, которые идеально им подходят. Поскольку такие турбины стоят недешево, стоит обратить внимание на неоригинальные модели, но выпускаемые известными изготовителями, имеющими лицензии на такое производство. В этом случае турбины на каждом этапе производства проходят тщательное тестирование.</p>
     </Div>
    </Group>
	</Panel>
);

Turbine.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Turbine;
