import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Cell, List, FormStatus} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
let thematics = [
    {
        "name": "Воздушный фильтр", 
        "to": "airfilter"
    }
]

let thematics2 = [
    {
        "name": "Глушитель",
        "to": "muffler"
    },
    {
        "name": "Коллектор",
        "to": "collector"
    },
    {
        "name": "Турбина",
        "to": "turbine"
    }
]

let thematics3 = [
    {
        "name": "Прошни",
        "to": "pistons"
    },
	{
        "name": "Коленчатый вал",
        "to": "crankshaft"
    },
    {
        "name": "Распределительный вал",
        "to": "camshaft"
    }
]

let thematics4 = [
    {
        "name": "Масляный фильтр",
        "to": "oilfilter"
    }
]

class Engine extends React.Component{
    constructor(props){
        super(props)
        this.state = {
        activePanel: 'engine', 
        activeStory: 'home', 
        search: ''
        }
        this.onChange = this.onChange.bind(this);

   }
   
    onChange (search) { this.setState({ search }); }

    get thematics () {
      const search = this.state.search.toLowerCase();
      return thematics.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
    }
    get thematics2 () {
      const search = this.state.search.toLowerCase();
      return thematics2.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
    }
    get thematics3 () {
      const search = this.state.search.toLowerCase();
      return thematics3.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
    }
    get thematics4 () {
      const search = this.state.search.toLowerCase();
      return thematics4.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
    }

    render(){
        let { go, id} = this.props;
        return(
    <Panel id={id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Категории
      </PanelHeader>
	   <FormStatus>
       Система очистки воздуха:
	   </FormStatus>
        <List>{this.thematics.map((thematic, i) => <Cell indicator={thematic.new} key={i} expandable onClick={go} data-to={thematic.to}>{thematic.name}</Cell>)}</List> 
	   <FormStatus>
       Выхлопная система:
	   </FormStatus>
        <List>{this.thematics2.map((thematic, i) => <Cell indicator={thematic.new} key={i} expandable onClick={go} data-to={thematic.to}>{thematic.name}</Cell>)}</List>
	   <FormStatus>
       Двигатель:
	   </FormStatus>
        <List>{this.thematics3.map((thematic, i) => <Cell indicator={thematic.new} key={i} expandable onClick={go} data-to={thematic.to}>{thematic.name}</Cell>)}</List> 
	   <FormStatus>
       Система смазки:
	   </FormStatus>
        <List>{this.thematics4.map((thematic, i) => <Cell indicator={thematic.new} key={i} expandable onClick={go} data-to={thematic.to}>{thematic.name}</Cell>)}</List> 
	</Panel>
        )
    }
}

Engine.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Engine;