import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const Crankshaft = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Коленчатый вал
      </PanelHeader>

    <Group>
     <Div>
      <center>Как работает коленчатый вал двигателя</center>
        <p>В камерах двигателя внутреннего сгорания, после воспламенения нагнетенного горючего, образуются газы, которые, расширяясь, толкают поршни. То есть, в свою очередь, оказывают воздействие на присоединенные к ним шатуны через кинематическую пару. Шатун нижней головкой через подшипник соединен с шейкой колена, расположенного на валу, и каждое движение поршня, таким образом, проворачивает весь коленчатый вал двигателя.</p>
        <p>Чтобы крутящий момент был передан на трансмиссию без ослабления, каждую коренную шейку охватывает специальный подшипник коленвала, состоящий из двух половинок, установленных внутри крышек картера. Между ячейками, по числу опорных шеек, располагаются подшипники, у каждого вместо элементов качения с внутренней стороны имеется канавка для масла.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Шестерня коленвала и ее значение</center>
        <p>Когда картер полностью собран, снаружи устанавливается сальник, а затем – шестерня коленвала. Необходима она для того, чтобы через зубчатый ремень или непосредственно через шестерню распределительного вала происходила его синхронизация с работой коленчатого вала. В свою очередь распредвал посредством установленных на нем кулачков с определенной периодичностью открывает и закрывает клапаны газораспределительного механизма.</p>
        <p>Если используется ременная передача, она попутно охватывает шкив насоса охлаждающей жидкости. К слову, натяжение ремня должно быть строго отрегулировано, для этого предусмотрен специальный ролик. Если у шестерни вдруг обнаружится люфт, проверьте, насколько надежно сидит в своем гнезде шпонка коленвала. Даже после того, как последняя будет вынута, шестерня при натянутом ремне должна сидеть достаточно плотно. Если люфт продолжается, значит, произошла деформация посадочного места, и не остается ничего другого, кроме как поменять вал.</p>
     </Div>
    </Group>
	</Panel>
);

Crankshaft.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Crankshaft;
