import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const WhatPG = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Что такое РКПП
      </PanelHeader>

    <Group>
     <Div>
      <center>Что такое РКПП?</center>
        <p>Роботизированная коробка передач — технология, которая своей конструкцией чем-то похожа на МКПП, однако такие функции, как отключение сцепления, переключения передач происходит автоматически. Слово «роботизированная» подразумевает то, что коробкой передач управляет электронное устройство. А водитель и дорожные условия создают лишь входные данные.</p>
        <p>Роботизированная коробка передач так же удобна, как и автоматическая. Но она совмещает качество и экономию механической коробки. Но «робот» отличается тем, что он имеет меньшую стоимость, чем АКПП. По этой причине множество современных производители автомобилей снабжают свое детище именно роботизированной коробкой передач. Независимо от того премиум это или дешевое транспортное средство.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Преимущества РКПП:</center>
          <p><li>Экономия масла — всего 3 литра. При равных объемах, стандартный автомат потребляет на три или четыре литра больше;</li>
          <li>Роботизированная трансмиссия достаточно ремонтопригодна. Теоретически любой сервис, изучив строение сможет ее починить;</li>
          <li>Ресурс сцепления приблизительно на 45 процентов выше, чем у МКПП и АКПП;</li>
          <li>Возможность сменить режим. То есть водитель сможет управлять авто, как на механики;</li>
          <li>Главный плюс — экономичность топлива, расход которого примерно равный МКПП.</li></p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Недостатки РКПП:</center>
          <p><li>Почти все «роботы» не программируемые. Поэтому для того чтобы сменить прошивку, нужно полностью поменять управленческий блок;;</li>
          <li>Необходимость перехода на нижнюю передачу заранее из-за задержки переключения коробки-робота;</li>
          <li>На многих роботизированных КП бывают рывки при смене передач или начале движения.</li></p>
     </Div>
    </Group>
	</Panel>
);

WhatPG.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default WhatPG;
