import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const LubCool = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Система смазки и охлаждения
      </PanelHeader>

    <Group>
     <Div>
      <center>Система смазки и охлаждения</center>
        <p>Очень важна для нормального функционирования коробки передач, поэтому в ней используется специальная гидравлическая жидкость ATF, именно она смазывает и охлаждает подвижные элементы. Охлаждение рабочей жидкости происходит в радиаторе охлаждения, который бывает внутренний и внешний. Внутренний радиатор (представляет собой теплообменник) располагается внутри радиатора охлаждающей жидкости двигателя. Также бывают более сложные теплообменники, которые имеют собственное жидкостное охлаждение, они устанавливаются на корпус коробки. Внешний располагается отдельно и представляет собой полноценный радиатор. На некоторых автомобилях в магистраль охлаждения от АКПП к радиатору встраивается термостат, регулирующий проходимый через него объём масла. Чтобы не допустить загрязнение каналов системы частицами, которые образуются при износе подвижных деталей устанавливается фильтр, он очищает рабочую жидкость.</p>
		<li>АКПП с внешним радиатором охлаждения масла.</li>
		<li>АКПП со встроенным радиатором охлаждения в радиатор двигателя.</li>
		<li>Радиатор охлаждения масла АКПП с системой жидкого охлаждения.</li>
     </Div>
    </Group>
	</Panel>
);

LubCool.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default LubCool;
