import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const Muffler = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Глушитель
      </PanelHeader>

    <Group>
     <Div>
      <center>Конструктивные особенности</center>
        <p>Система отвода отработанных газов автомобиля позволяет не только удалять продукты сгорания топлива, но и увеличить эффективность работы силового агрегата. Для этого в системе используется такой узел, как глушитель, который способен подавлять шум, создаваемый выхлопными газами, и значительно снизить уровень их токсичности.</p>
        <p>Независимо от того, изготавливается ли глушитель в заводских условиях, или же собирается своими руками, в его конструкции должны быть следующие обязательные элементы:</p>
          <li>Выпускной коллектор - соединяется непосредственно с двигателем через приемную трубу, которая может быть оборудована виброкомпенсатором.</li>
          <li>Отработанные газы - из приемной трубы попадают в полость катализатора, в котором догорают остатки топлива, и снижается насыщенность углекислого газа.</li>
          <li>Из катализатора - газовые потоки устремляются в камеру резонатора, где гасятся колебания и уравновешивается воздействие их пульсации.</li>
		  <li>Объем отработанных газовых потоков - проходит через камеры заднего глушителя, который подавляет шум.</li>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Принцип работы</center>
        <p>Если посмотреть на устройство в разрезе, то можно увидеть множество металлических или керамических перегородок, термостойкую вату, а также присутствие перфорированных труб. Наличие этих элементов в конструкции глушителя замедляет скорость движения газовых потоков.</p>
        <p>Если вы решили модернизировать отдельные элементы системы отвода отработанных газов, то необходимо учитывать некоторые нюансы:</p>
          <li>На мощных двигателях автомобилей должен стоять резонатор достаточно большого объема, который будет обеспечивать необходимый уровень сглаживания выхлопа.</li>
          <li>Количество перегородок внутри камер, которые сглаживают неравномерность отводимых потоков.</li>
     </Div>
    </Group>
	</Panel>
);

Muffler.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Muffler;
