import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const AirFilter = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Поршни
      </PanelHeader>

    <Group>
     <Div>
      <center>Поршни</center>
        <p>Поршень — деталь цилиндрической формы, совершающая возвратно-поступательное движение внутри цилиндра двигателя. Нужен для изменения давления газа в механическую работу, или наоборот — возвратно-поступательного движения в изменение давления. То есть он передаёт на шатун усилие, возникающее от давления газов и обеспечивает протекание всех тактов рабочего цикла.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Требования к поршням двигателя</center>
          <li>Поршень, перемещаясь в цилиндре, позволяет расширяться сжатым газам, продукту горения топлива, и совершать механическую работу. Следовательно, он должен быть устойчивым к высокой температуре, давлению газов и надежно уплотнять канал цилиндра.</li>
          <li>Он должен наилучшим образом отвечать требованиям пары трения с целью минимизировать механические потери и, как следствие, износа.</li>
          <li>Испытывая нагрузки со стороны камеры сгорания и реакцию от шатуна, он должен выдерживать механическое воздействие.</li>
		  <li>Совершая возвратно-поступательное движение с высокой скоростью, должен как можно меньше нагружать кривошипно-шатунный механизм инерционными силами.</li>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Основное назначение поршней в работе двигателя</center>
        <p>Топливо, сгорая в надпоршневом пространстве, выделяет огромное количество тепла в каждом цикле работы двигателя. Температура сгоревших газов достигает 2000 градусов. Только часть энергии они передадут движущимся деталям мотора, все остальное в виде тепла нагреет двигатель. То, что останется, вместе с отработанными газами улетит в трубу. Следовательно, если не будем охлаждать поршень, он через некоторое время расплавится.</p>
     </Div>
    </Group>
	</Panel>
);

AirFilter.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default AirFilter;
