import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const OperationMG = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Принципы функционирования
      </PanelHeader>

    <Group>
     <Div>
      <center>Принципы функционирования</center>
        <p>Допустим, скорость вращения коленвала составляет 1000 об/мин и благодаря механизму сцепления соответствующий крутящий момент получает первичный вал коробки передач. Если теперь включить первую передачу, то жесткозакрепленная на этом валу шестерня войдет в зацепление с другой шестерней, которая крупнее по размеру и имеет в четыре раза, например, больше зубьев.</p>
		<p>Следовательно, вал, на котором установлена вторая шестерня, будет вращаться со скоростью в четыре раза меньше скорости коленвала, то есть 250 об/мин. Скорость вращения уменьшилась в четыре раза согласно соотношению зубьев шестерен, а крутящий момент в такое же число раз увеличится на второй шестерне.</p>
		<p>Применение различных передаточных чисел пар шестерен позволяет получать от двигателя и передавать далее на ведущие колеса автомобиля разные крутящие моменты. Иначе говоря, вращение коленвала двигателя в 1000 об/мин можно преобразовать, при выборе соответствующих передач, во вращение ведущих колес автомобиля со скоростью, например, в 333 или 250 об/мин и т.д.</p>
		<p>Реализация возможности передвижения автомобиля задним ходом обеспечивается наличием в коробке передач соответствующего механизма, состоящего из дополнительного вала и установленной на нем шестерни заднего хода. Если выбрать задний ход, то между вторичным и промежуточным валами будет дополнительно включена эта шестерня, благодаря которой вторичный вал коробки передач будет вращаться в противоположную, по сравнению с обычным направлением, сторону за счет нечетного количества сцепленных друг с другом шестерен.</p>
     </Div>
    </Group>
	</Panel>
);

OperationMG.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default OperationMG;
