import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const Collector = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Коллектор
      </PanelHeader>

    <Group>
     <Div>
      <center>Строение выпускного и впускного коллектора</center>
        <p>Количество труб, что у впускного, что у выпускного коллектора напрямую зависит от числа цилиндров в двигателе.</p>
        <p>Теперь подробнее о деталях, с которыми сопрягаются оба коллектора:</p>
          <li>Впускной - является частью системы питания, и к нему подключен дроссельный узел. У современных дизелей вместо всего этого стоит аккумуляторная топливная система, более известная как «Common Rail».</li>
          <li>Выпускной - соединяется с приемной трубой, далее идет катализатор, резонатор и глушитель. На старых автомобилях катализатор отсутствует.</li>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Устройство впускного коллектора</center>
        <p>Предназначение впускного коллектора заключается в подведении топливно-воздушной смеси или только воздуха к цилиндрам.</p>
		<p>Обычно коллектор – металлический, но иногда встречается из специального пластика, выдерживающего высокие температуры. Так делают для снижения стоимости и для облегчения веса мотора.</p>
		<p>Соединяется впускной коллектор разветвленной частью с головкой блока цилиндров, через прокладку. При открывании впускных клапанов создается разряжение, с помощью которого топливно-воздушная смесь попадает в цилиндр, после чего клапана закрываются, и начинается такт сжатия. Потом они открываются, и смесь топлива с воздухом попадает в цилиндр.</p>
		<p>Если же впрыск непосредственный, и топливо подается сразу в камеру сгорания, через коллектор проходит только воздух, а смешение происходит прямо в цилиндре.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Устройство выпускного коллектора</center>
        <p>Задача выпускного коллектора – отведение выхлопных газов. На такте выпуска одноименные клапана открываются, и под воздействием движущегося наверх поршня газы попадают в коллектор. Пройдя через коллектор, выхлопные газы попадают в приемную трубу, далее в катализатор, где оседает значительная часть вредных веществ, потом в резонатор, снижающий громкость выхлопа, затем в глушитель, где звук исчезает полностью, и отводятся в атмосферу. У моторов с турбонаддувом газы после коллектора оказываются в специальном канале и крутят турбину, и только потом уходят в приемную трубу.</p>
     </Div>
    </Group>
	</Panel>
);

Collector.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Collector;
