import React from 'react';
import connect from '@vkontakte/vkui-connect';
import '@vkontakte/vkui/dist/vkui.css';
import {View} from '@vkontakte/vkui';

import Home from './panels/Home';
import Other from './panels/Other';
import ServInfo from './panels/ServInfo';
import ContInfo from './panels/ContInfo';

import Engine from './panels/Engine';
import AirFilter from './panels/AirFilter';
import Muffler from './panels/Muffler';
import Collector from './panels/Collector';
import Turbine from './panels/Turbine';
import Pistons from './panels/Pistons';
import Crankshaft from './panels/Crankshaft';
import Camshaft from './panels/Camshaft';
import OilFilter from './panels/OilFilter';

import Transmission from './panels/Transmission';
import WhatMG from './panels/WhatMG';
import OperationMG from './panels/OperationMG';
import WhatAG from './panels/WhatAG';
import TransmissionAG from './panels/TransmissionAG';
import LubCool from './panels/LubCool';
import WhatPG from './panels/WhatPG';
import DevicePG from './panels/DevicePG';
import OperationPG from './panels/OperationPG';

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activePanel: 'home', 
        };
        }

    componentDidMount() {
       window.addEventListener('popstate', e => e.preventDefault() & this.menu(e));
        window.history.pushState( { panel: 'home' }, `home` );
        connect.subscribe((e) => {
            switch (e.detail.type) {
                case 'VKWebAppGetUserInfoResult':
                    this.setState({ fetchedUser: e.detail.data, activePanel: 'home' });
                    break;
                default:
                    console.log(e.detail.type);
            }
        });
        connect.send('VKWebAppGetUserInfo', {});
    }

 onHeaderEditClick() {
    this.setState({ editing: !this.state.editing });
  }

  getForm() {
    return new Promise((resolve) => setTimeout(() => resolve(), 1000));
  }


    go = (e) => {
        this.setState({ activePanel: e.currentTarget.dataset.to })
        window.history.pushState( { panel: e.currentTarget.dataset.to }, `${e.currentTarget.dataset.to}` );
    }

    menu (e) {
        if(e.state) {
            this.setState( { activePanel: e.state.panel } );
        } else {
            this.setState( { activePanel: 'home', search: '' } );
            window.history.pushState( { panel: 'home' }, `home` );
        }
    }

render() {
        return (
            <View popout={this.state.popout} activePanel={this.state.activePanel}>
                <Home id="home" change={this.ChangeSearch} go={this.go} state={this.state} />
                <Other id="other" go={this.go} />
                <ServInfo id="servinfo" go={this.go} />
				<ContInfo id="continfo" go={this.go} />
				
				<Engine id="engine" go={this.go} />
				<AirFilter id="airfilter" go={this.go} />
				<Muffler id="muffler" go={this.go} />
				<Collector id="collector" go={this.go} />
				<Turbine id="turbine" go={this.go} />
				<Pistons id="pistons" go={this.go} />
				<Crankshaft id="crankshaft" go={this.go} />
				<Camshaft id="camshaft" go={this.go} />
				<OilFilter id="oilfilter" go={this.go} />
				
				<Transmission id="transmission" go={this.go} />
				<WhatMG id="whatmg" go={this.go} />
				<OperationMG id="operationmg" go={this.go} />
				<WhatAG id="whatag" go={this.go} />
				<TransmissionAG id="transmissionag" go={this.go} />
				<LubCool id="lubcool" go={this.go} />
				<WhatPG id="whatpg" go={this.go} />
				<OperationPG id="operationpg" go={this.go} />
				<DevicePG id="devicepg" go={this.go} />
            </View>
               );
    }
}

export default App;
