import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, Search, ScreenSpinner, Epic, TabbarItem,  Tabbar,  platform, IOS, Cell, List, View, Div} from '@vkontakte/vkui';
import connect from '@vkontakte/vkui-connect';
import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon28More from '@vkontakte/icons/dist/28/more';

const osname = platform();
let thematics = [
    {
        "name": "Двигатель",
        "to" : "engine"
    },
    {
        "name": "Трансмиссия",
        "to" : "transmission"
    }
]

class Home extends React.Component{
    constructor(props){
        super(props)
        this.state = {  
        activeStory: 'home', 
        popout: null,
        map: false, 
        subscriber: false,
        contextOpened: true,
        mode: 'rate', 
        search: ''
        }
        this.onStoryChange = this.onStoryChange.bind(this);
        this.onChange = this.onChange.bind(this);
    }

    onChange (search) { this.setState({ search }); }
    
    get thematics () {
      const search = this.state.search.toLowerCase();
      return thematics.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
    }
    subscribe () {
    this.setState({ subscriber: true });
    connect.send("VKWebAppJoinGroup", {"group_id": 185895387});
    } 
    onClick () {
    this.setState({ popout: <ScreenSpinner /> });
    setTimeout(() => { this.setState({ popout: null }) }, 2000);
    }
     onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story })
    }
    render(){
        let { go} = this.props;
        return(
       <Epic activeStory={this.state.activeStory} tabbar={
        <Tabbar>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'home'}
            data-story="home"
            text = "Детали"
          ><Icon28More /></TabbarItem>
        </Tabbar>
      }>
        <View id="home" activePanel="home">
          <Panel id="home"> 
<PanelHeader
             left={<HeaderButton onClick={go} data-to ="other">
                        {osname === IOS ? <Icon24List/> : <Icon24List/>}
                    </HeaderButton>} >
                    Детали
                    </PanelHeader> 
<Search maxlength="65" value={this.state.search} onChange={this.onChange}/> 
<List> 
 {this.thematics.map((thematic, i) => <Cell indicator={thematic.new} key={i} expandable onClick={go} data-to={thematic.to}>{thematic.name}</Cell>)
} 
</List> 
<Div></Div>
{this.thematics.length === 0 ? <div style={{textAlign: "center", color: "#808080"}}><br/>По Вашему запросу ничего не найдено. <br/>Новые разделы находятся в разработке!</div> : null 
}
   </Panel> 
</View>

      </Epic>
    )
  }
}
Home.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Home;