import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, CellButton, platform, IOS, Group, FormLayout, FormStatus} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Flash from '@vkontakte/icons/dist/24/flash';

function openTab(url) {

// Create link in memory

let a = window.document.createElement("a");

a.target = '_blank';

a.href = url;

// Dispatch fake click

let e = window.document.createEvent("MouseEvents");

e.initMouseEvent("click", true, true, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);

a.dispatchEvent(e);

};

const osname = platform();
const ContInfo = ({ go, nickname, fetchedUser, onClick, usersTop, userAcc, name, updateUserNickname, id}) => (


	<Panel id={id}>
	  <PanelHeader
		left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
		</HeaderButton>}>
          Контактная информация
	  </PanelHeader>
	  
	<Group>
		<FormLayout>
        <FormStatus><div style={{color: "#708090"}}>Не могу зайти в сервис, не грузится страница, что делать?</div>Чаще всего проблема кроется в проблеме с кэшом, попробуйте почистить его.</FormStatus>
        <FormStatus><div style={{color: "#FFDD45"}}>Внимание!</div>Если у Вас возникли вопросы, то напишите в сообщения группы.</FormStatus>
        </FormLayout>
	</Group>
	<Group>
      <CellButton onClick={() => openTab("https://vk.com/public185895387")}  before={<Icon24Flash />}>Мы ВКонтакте</CellButton>
    </Group>
	</Panel>
);

ContInfo.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default ContInfo;
