import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Cell, List, Group, FormLayout} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
let thematics = [
    {
        "name": "Информация о сервисе", 
        "to": "servinfo"
    },
    {
        "name": "Контактная информация",
        "to": "continfo"
    }
]

class Other extends React.Component{
    constructor(props){
        super(props)
        this.state = {
        activePanel: 'other', 
        activeStory: 'home', 
        search: ''
        }
        this.onChange = this.onChange.bind(this);

   }
    onChange (search) { this.setState({ search }); }


    get thematics () {
      const search = this.state.search.toLowerCase();
      return thematics.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
    }
	
    render(){
        let { go, id} = this.props;
        return(
    <Panel id={id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Прочее
      </PanelHeader>
	  <Group>
	  <FormLayout>
        <List>{this.thematics.map((thematic, i) => <Cell indicator={thematic.new} key={i} expandable onClick={go} data-to={thematic.to}>{thematic.name}</Cell>)}</List>
    	  </FormLayout>
	  </Group>
	</Panel>
        )
    }
}

Other.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Other;