import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const Camshaft = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Распределительный вал
      </PanelHeader>

    <Group>
     <Div>
      <center>Принцип работы распределительного вала</center>
        <p>Распредвал находится в развале блока цилиндров. С помощью зубчатой или цепной передачи распредвал приводится в действие от коленчатого вала.</p>
        <p>Вращение распределительного вала обеспечивает воздействие кулачков на работу впускного и выпускного клапанов. Это происходит в строгом соответствии с фазами газораспределения и порядком работы цилиндров двигателя.</p>
		<p>Для правильной установки фаз газораспределения существуют установочные метки, расположенные на распределительных шестернях или на приводном шкиве. С этой же целью кривошипы коленвала и кулачки распредвала должны быть в строго определенном положении, относительно друг друга.</p>
		<p>Благодаря установке, производимой по меткам, соблюдается последовательность чередования тактов – порядок работы цилиндров двигателя. Порядок работы цилиндров зависит от их расположения и конструктивных особенностей коленвала и распредвала.</p>
     </Div>
    </Group>

    <Group>
     <Div>
      <center>Рабочий цикл двигателя</center>
        <p>Период, когда впускной и выпускной клапаны в каждом цилиндре должны открыться один раз — это и есть рабочий цикл двигателя. Он осуществляется за 2 оборота коленвала. В это время распредвал должен сделать один оборот. Именно для этого и шестерня распредвала имеет в два раза больше зубьев.</p>
     </Div>
    </Group>
	</Panel>
);

Camshaft.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Camshaft;
