import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const OperationPG = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Принцип работы
      </PanelHeader>

    <Group>
     <Div>
      <center>Принцип работы</center>
        <p>Роботизированная коробка может работать в двух режимах:</p>
		<li>Автоматический - Для управления КП используется специальный алгоритм и данные, полученные из внешней среды входными датчиками.</li>
		<li>Полуавтоматический - Позволяет менять передачи последовательно с самой низкой на высшую и обратно. Такие операции делаются рычагом селектора или переключателями под рулем. Поэтому такая коробка иногда называется секвентальной.</li>
     </Div>
    </Group>
	</Panel>
);

OperationPG.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default OperationPG;
