import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Div,  platform, IOS, Group} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();
const WhatMG = props => (
	<Panel id={props.id}>
      <PanelHeader
        left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
        </HeaderButton>}>
          Что такое АКПП
      </PanelHeader>

    <Group>
     <Div>
      <center>Что такое АКПП?</center>
        <p>Автоматическая коробка передач (АКП, встречается АКПП) — разновидность трансмиссии автомобилей, обеспечивающая автоматический (без прямого участия водителя) выбор соответствующего текущим условиям движения передаточного числа, в зависимости от множества факторов</p>
		<p>АКПП устроено достаточно сложно и включает в себя следующие механизмы:</p>
		<li>гидротрансформатор – обеспечивает передачу и изменение крутящего момента от силового агрегата;</li>
        <li>коробка переключения передач – преобразует усилие и осуществляет привод колёс;</li>
        <li>система управления — управляет рабочей жидкостью;</li>
        <li>система смазки и охлаждения – создаёт давление и циркуляцию в системе.</li>
     </Div>
    </Group>
	</Panel>
);

WhatMG.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default WhatMG;
